import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'

const entity = {
  createdAt: {
  },
  totalAmount: {
  },
  leftAmount: {
  },
  longId: {
  },
}
const config = {
  load: async function (filter) {
    return (await hillo.get('MemberCard.php', {
      op: 'showAllMemberCardSaleRecord',
      ...filter,
    }))
      .content
  },
}

export default IKDataEntity.ModelFactory(entity, config)
